import React from 'react';

function DotIcon () {
    return (
        <svg
            width="15"
            height="15"
            viewBox="0 0 24 24"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
        >
            <circle cx="12" cy="12" r="3" fill="#808080"/>
        </svg>
    );
}

export default DotIcon;